* { box-sizing: border-box; margin:0; padding:0; }
body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  background: linear-gradient(180deg,#0b1220,#020617);
  color: #e5e7eb;
  min-height:100vh;
}

/* layout */
.app { display:flex; flex-direction:column; min-height:100vh; }
.header { padding:18px; text-align:center; border-bottom:1px solid #1f2937; background:rgba(2,6,23,0.6); }
.header p { color:#9ca3af; margin-top:6px; font-size:0.9rem; }

/* main area */
.main { flex:1; display:flex; align-items:flex-start; justify-content:center; padding:28px; }

/* form + preview shell */
.form-shell { display:grid; grid-template-columns: 420px 360px; gap:20px; width:100%; max-width:960px; }

/* form card */
.form-card {
  background: rgba(2,6,23,0.7);
  border:1px solid #1f2937;
  padding:16px;
  border-radius:12px;
  box-shadow: 0 14px 30px rgba(0,0,0,0.6);
}
.form-row { margin-bottom:12px; display:flex; flex-direction:column; gap:6px; }
.form-row.small { max-width:140px; }
label { font-size:0.85rem; color:#9ca3af; }
input, textarea {
  padding:8px 10px; border-radius:8px; border:1px solid #2b3340;
  background:#020617; color:#e5e7eb; outline:none;
}
input:focus, textarea:focus { border-color:#3b82f6; box-shadow:0 6px 18px rgba(59,130,246,0.12); }

.error { color:#fb7185; font-size:0.82rem; margin-top:4px; }
.note { margin-top:8px; color:#9ca3af; font-size:0.82rem; }
.success { margin-top:10px; color:#86efac; font-weight:600; }

/* actions */
.form-actions { display:flex; gap:10px; margin-top:6px; }
.btn {
  padding:10px 14px; border-radius:999px; border:none; cursor:pointer;
  background:#3b82f6; color:#fff; font-weight:600;
}
.btn.outline { background:transparent; border:1px solid #374151; color:#e5e7eb; }
.btn:disabled { background:#374151; cursor:not-allowed; color:#9ca3af; }

/* preview */
.preview { background: rgba(7,10,16,0.6); border-radius:12px; padding:14px; border:1px solid #1f2937; }
.preview h3 { margin-bottom:8px; color:#38bdf8; }
.preview-row { margin-bottom:8px; color:#cbd5e1; }
.validation-summary { margin-top:12px; padding-top:8px; border-top:1px dashed #1f2937; color:#9ca3af; font-size:0.9rem; }

/* footer */
.footer { padding:12px; text-align:center; border-top:1px solid #1f2937; color:#9ca3af; }
